import { Vec3, director } from "cc";
import { GameMgr, GameMode } from "../../module_basic/scripts/GameMgr";
import { SubGameMgr } from "../../module_basic/scripts/SubGameMgr";
import { UserMgr } from "../../module_basic/scripts/UserMgr";
import { IBilliardsGameData, IBilliardsPlayer } from "../../module_basic/shared/protocols/public/billiards/BilliardsTypeDef";
import { MsgAdjustCue } from "../../module_basic/shared/protocols/public/billiards/MsgAdjustCue";
import { MsgGameBeginPush } from "../../module_basic/shared/protocols/public/billiards/MsgGameBeginPush";
import { MsgGameDataChangedPush } from "../../module_basic/shared/protocols/public/billiards/MsgGameDataChangedPush";
import { MsgGameDataSyncPush } from "../../module_basic/shared/protocols/public/billiards/MsgGameDataSyncPush";
import { MsgGameOverPush } from "../../module_basic/shared/protocols/public/billiards/MsgGameOverPush";
import { MsgHitBall } from "../../module_basic/shared/protocols/public/billiards/MsgHitBall";
import { MsgHitBallComplete } from "../../module_basic/shared/protocols/public/billiards/MsgHitBallComplete";
import { MsgPlayerComesPush } from "../../module_basic/shared/protocols/public/billiards/MsgPlayerComesPush";
import { MsgPlayerDataChangedPush } from "../../module_basic/shared/protocols/public/billiards/MsgPlayerDataChangedPush";
import { MsgPlayerLeavesPush } from "../../module_basic/shared/protocols/public/billiards/MsgPlayerLeavesPush";
import { MsgBallsDataSync } from "../../module_basic/shared/protocols/public/billiards/MsgBallsDataSync";
import { gameNet } from "../../module_basic/scripts/NetGameServer";
import { RoomMgr } from "../../module_basic/scripts/RoomMgr";
import { SceneDef } from "../../scripts/SceneDef";
import { SubGameDef } from "../../module_basic/scripts/SubGameDef";
import { ModuleDef } from "../../scripts/ModuleDef";

export class BilliardsGameEvent {
    public static ROOM_PUSH: string = 'BilliardsGameEvent.ROOM_PUSH';
    public static HIT_BALL: string = 'BilliardsGameEvent.HIT_BALL';
    public static HIT_BALL_COMPLETE: string = 'BilliardsGameEvent.HIT_BALL_COMPLETE';
    public static CUE_CHANGED: string = 'BilliardsGameEvent.CUE_CHANGED';
    public static BALL_SYNC: string = 'BilliardsGameEvent.BALL_SYNC';

    public static TURN_PLAYER_CHANGED: string = 'BilliardsGameEvent.TURN_PLAYER_CHANGED';
    public static PLAYER_DATA_CHANGED: string = 'BilliardsGameEvent.PLAYER_DATA_CHANGED';

    public static GAME_BEGIN: string = 'BilliardsGameEvent.GAME_BEBIN';
    public static GAME_OVER: string = 'BilliardsGameEvent.GAME_OVER';

    public static PLAYER_COMES: string = 'BilliardsGameEvent.PLAYER_COMES';
    public static PLAYER_LEAVES: string = 'BilliardsGameEvent.PLAYER_LEAVES';
}


export class BilliardsGameMgr extends GameMgr {
    private static _inst: BilliardsGameMgr;
    public static get inst(): BilliardsGameMgr {
        if (!this._inst) {
            this._inst = new BilliardsGameMgr();
        }
        return this._inst;
    }

    constructor(){
        super();
        this._gameType = SubGameDef.BILLIARDS;
        this._entryScene = { name: 'lobby_billiards', bundle: ModuleDef.BILLIARDS };
        this._gameScene = { name: 'game_billiards', bundle: ModuleDef.BILLIARDS };
        this.initNetMsgHandlers();
    }

    protected _gameData: IBilliardsGameData;
    order: number;
    cueDir: Vec3;

    public get gameData(): IBilliardsGameData {
        return this._gameData;
    }

    initNetMsgHandlers() {
        gameNet.listenMsg("billiards/GameDataSyncPush", this.onNet_GameDataSyncPush, this);
        gameNet.listenMsg("billiards/GameDataChangedPush", this.onNet_GameDataChangedPush, this);

        gameNet.listenMsg("billiards/PlayerComesPush", this.onNet_PlayerComesPush, this);
        gameNet.listenMsg("billiards/PlayerDataChangedPush", this.onNet_PlayerDataChangedPush, this);
        gameNet.listenMsg("billiards/PlayerLeavesPush", this.onNet_PlayerLeavesPush, this);

        gameNet.listenMsg("billiards/GameBeginPush", this.onNet_GameBeginPush, this);
        gameNet.listenMsg("billiards/GameOverPush", this.onNet_GameOverPush, this);

        gameNet.listenMsg("billiards/HitBall", this.onNet_HitBallPush, this);
        gameNet.listenMsg("billiards/HitBallComplete", this.onNet_HitBallCompletePush, this);
        gameNet.listenMsg("billiards/AdjustCue", this.onNet_AdjustCuePush, this);
    }

    public reset() {
        super.reset();
        this._gameData = null;
        this.cueDir = null;
        this.cueDir = null;
    }

    initSoloMode(){
        RoomMgr.inst.reset();
        this.reset();
        SubGameMgr.gameMgr = this;
    }

    public get selfPlayer(): IBilliardsPlayer {
        return this.getPlayer(UserMgr.inst.uid);
    }

    private get selfPlayerIndex(): number {
        return this.getPlayerIndex(UserMgr.inst.uid);
    }

    public get leftUserId(): string {
        if (!this._gameData || this._gameData.players.length == 0) {
            return '';
        }

        if (RoomMgr.inst.isPlayer) {
            return UserMgr.inst.uid;
        }
        else {
            return this._gameData.players[0].uid;
        }
    }

    public get rightUserId(): string {
        if (!this._gameData || this._gameData.players.length == 0) {
            return '';
        }

        if (RoomMgr.inst.isPlayer) {
            if (this._gameData.players[0].uid != UserMgr.inst.uid) {
                return this._gameData.players[0].uid;
            }
            else {
                let p = this._gameData.players[1];
                return p ? p.uid : '';
            }
        }
        else {
            let p = this._gameData.players[1];
            return p ? p.uid : '';
        }
    }

    public get isMyTurn(): boolean {
        if (!this._gameData) {
            return false;
        }

        if (!RoomMgr.inst.isPlayer) {
            return false;
        }
        if (!this._gameData.currentPlayer) {
            return false;
        }
        return this._gameData.currentPlayer == UserMgr.inst.uid;
    }

    public getPlayer(userId: string): IBilliardsPlayer {
        if (!this._gameData || !this._gameData.players) {
            return null;
        }
        for (let i = 0; i < this._gameData.players.length; ++i) {
            let p = this._gameData.players[i];
            if (p.uid == userId) {
                return p;
            }
        }
        return null;
    }

    private getPlayerIndex(userId: string): number {
        for (let i = 0; i < this._gameData.players.length; ++i) {
            if (this._gameData.players[i].uid == userId) {
                return i;
            }
        }
        return -1;
    }

    public isPocketed(ballId: number): boolean {
        if (!this._gameData || !this._gameData.players) {
            return false;
        }
        for (let i = 0; i < this._gameData.players.length; ++i) {
            let p = this._gameData.players[i];
            if (p.pocketedBalls && p.pocketedBalls.indexOf(ballId) != -1) {
                return true;
            }
        }
        return false;
    }

    sendMsg_HitBall(msg: MsgHitBall) {
        gameNet.sendMsg("billiards/HitBall", msg);
    }

    sendMsg_HitBallComplete(msg: MsgHitBallComplete) {
        gameNet.sendMsg("billiards/HitBallComplete", msg);
    }

    sendMsg_AdjustCue(msg: MsgAdjustCue) {
        gameNet.sendMsg("billiards/AdjustCue", msg);
    }

    sendMsg_BallsDataSync(msg: MsgBallsDataSync) {
        gameNet.sendMsg("billiards/BallsDataSync", msg);
    }

    // ============= MESSAGE HANDLER ============
    /**
     * This message will arrive before login result.
     * 
    */
    onNet_GameDataSyncPush(msg: MsgGameDataSyncPush) {
        this._gameData = msg;
        console.log('game data',msg);
        director.emit(BilliardsGameEvent.ROOM_PUSH);
    }

    onNet_GameDataChangedPush(msg: MsgGameDataChangedPush) {
        if (!this._gameData) {
            return;
        }
        if (msg.ballsData !== undefined) {
            this._gameData.ballsData = msg.ballsData;
            if (!this.isMyTurn) {
                director.emit(BilliardsGameEvent.BALL_SYNC, msg.ballsData);
            }
        }

        if (msg.order !== undefined) {
            this.order = msg.order;
        }
        if (msg.cueDir !== undefined) {
            if (!this.cueDir) {
                this.cueDir = new Vec3();
            }
            this.cueDir.set(msg.cueDir.x, msg.cueDir.y, msg.cueDir.z);
        }

        if (msg.currentPlayer !== undefined) {
            this._gameData.currentPlayer = msg.currentPlayer;
            director.emit(BilliardsGameEvent.TURN_PLAYER_CHANGED, msg.currentPlayer);
        }
    }

    onNet_PlayerComesPush(msg: MsgPlayerComesPush) {
        this._gameData.players.push(msg.player);
        director.emit(BilliardsGameEvent.PLAYER_COMES, msg.player);
    }

    onNet_PlayerDataChangedPush(msg: MsgPlayerDataChangedPush) {
        let p = this.getPlayer(msg.uid);
        if (!p) {
            return;
        }

        if (msg.pocketedBalls != undefined) {
            p.pocketedBalls = msg.pocketedBalls;
        }

        director.emit(BilliardsGameEvent.PLAYER_DATA_CHANGED, msg);
    }

    onNet_PlayerLeavesPush(msg: MsgPlayerLeavesPush) {
        for (let i = 0; i < this._gameData.players.length; ++i) {
            let p = this._gameData.players[i];
            if (p.uid == msg.uid) {
                this._gameData.players.splice(i, 1);
                director.emit(BilliardsGameEvent.PLAYER_LEAVES, msg.uid);
                break;
            }
        }
    }

    onNet_GameBeginPush(msg: MsgGameBeginPush) {
        if (!this._gameData) {
            return;
        }

        this._gameData.ballsData = null;
        this._gameData.players.forEach(v => {
            v.pocketedBalls = [];
        });

        director.emit(BilliardsGameEvent.GAME_BEGIN);
    }

    onNet_GameOverPush(msg: MsgGameOverPush) {
        director.emit(BilliardsGameEvent.GAME_OVER, msg);
    }

    onNet_HitBallPush(msg: MsgHitBall) {
        if (!this._gameData) {
            return;
        }
        if (!this.isMyTurn) {
            director.emit(BilliardsGameEvent.HIT_BALL, msg.hitImpulse, msg.order);
        }
    }

    onNet_HitBallCompletePush(msg: MsgHitBallComplete) {
        if (!this._gameData) {
            return;
        }
        director.emit(BilliardsGameEvent.HIT_BALL_COMPLETE);
    }

    onNet_AdjustCuePush(msg: MsgAdjustCue) {
        if (!this._gameData) {
            return;
        }
        if (msg.direction) {
            if (!this.cueDir) {
                this.cueDir = new Vec3();
            }
            this.cueDir.set(msg.direction.x, msg.direction.y, msg.direction.z);
        }
        if (!this.isMyTurn) {
            director.emit(BilliardsGameEvent.CUE_CHANGED, msg);
        }
    }
}

SubGameMgr.inst.registerGameMgr(BilliardsGameMgr.inst);